.container{
  background-color:white;
  background-size: cover;
  background-position: center;
}
.box1{
  /* padding: 200px; */
  background-image:url("https://c.stocksy.com/a/XR5500/z9/1212567.jpg");
  width:100%;
  height:300px ;
  border: 5px solid white;
  border-radius: 50px 50px;
  border-width: 20px;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.col{
  align-items: center !important;
}

.box2{
  width:100%;
  height:400px ;
  border: 5px solid white;
  border-radius: 50px 50px;
  border-width: 20px;
  text-align: center !important;
}

html, body {
  margin:0px;
  height:100%;
}

.picContainer {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.img1{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 170px;
  height: 170px;
  border-radius: 50%;
  object-position: 50% 50%;
}

.textContainer{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  line-height: 1.8;
  text-align: center !important;
}

.fav{
  text-align: center;
}
